'use client';

import { useState, useEffect, FormEvent } from 'react';
import axios from '@/lib/axios';
import { useRouter } from 'next/navigation';
import { toast } from 'sonner';
import Link from 'next/link';
import { ArrowLeft, Plus, Trash2, Save, Loader2 } from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import {
    Select, SelectContent, SelectItem, SelectTrigger, SelectValue,
} from "@/components/ui/select";
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Supplier, PaymentMethod, BahanBaku } from '@/types';

export default function CreatePembelianPage() {
    const router = useRouter();
    const [isSaving, setIsSaving] = useState(false);

    // Data Master (untuk Dropdown)
    const [suppliers, setSuppliers] = useState<Supplier[]>([]);
    const [payments, setPayments] = useState<PaymentMethod[]>([]);
    const [bahanBakus, setBahanBakus] = useState<BahanBaku[]>([]);

    // Form Header (Data Faktur)
    const [formData, setFormData] = useState({
        supplier_id: '',
        payment_method_id: '',
        tanggal_pembelian: new Date().toISOString().split('T')[0],
        no_faktur_supplier: '',
        catatan: '',
    });

    const [items, setItems] = useState([
        { bahan_baku_id: '', jumlah: '', harga_beli_satuan: '', subtotal: 0 }
    ]);

    useEffect(() => {
        const initData = async () => {
            try {
                const [resSup, resPay, resBahan] = await Promise.all([
                    axios.get('/api/suppliers'),
                    axios.get('/api/paymentMethods'),
                    axios.get('/api/bahanBakus')
                ]);
                setSuppliers(resSup.data);
                setPayments(resPay.data);
                setBahanBakus(resBahan.data);
            } catch (error) {
                toast.error("Gagal memuat data master.");
            }
        };
        initData();
    }, []);

    const addItem = () => {
        setItems([...items, { bahan_baku_id: '', jumlah: '', harga_beli_satuan: '', subtotal: 0 }]);
    };

    const removeItem = (index: number) => {
        if (items.length === 1) return;
        const newItems = items.filter((_, i) => i !== index);
        setItems(newItems);
    };

    const updateItem = (index: number, field: string, value: string) => {
        const newItems = [...items];
        // @ts-ignore
        newItems[index][field] = value;

        const qty = parseFloat(newItems[index].jumlah) || 0;
        const price = parseFloat(newItems[index].harga_beli_satuan) || 0;
        newItems[index].subtotal = qty * price;

        setItems(newItems);
    };

    const grandTotal = items.reduce((sum, item) => sum + item.subtotal, 0);

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        
        if (!formData.supplier_id || !formData.payment_method_id) {
            toast.error("Data Supplier dan Pembayaran wajib diisi.");
            return;
        }
        if (items.some(i => !i.bahan_baku_id || !i.jumlah || !i.harga_beli_satuan)) {
            toast.error("Mohon lengkapi data barang (Nama, Jumlah, Harga).");
            return;
        }

        setIsSaving(true);
        try {
            const payload = {
                ...formData,
                total_harga_faktur: grandTotal,
                items: items.map(i => ({
                    bahan_baku_id: parseInt(i.bahan_baku_id),
                    jumlah: parseFloat(i.jumlah),
                    harga_beli_satuan: parseFloat(i.harga_beli_satuan)
                }))
            };

            await axios.post('/api/pembelians', payload);
            
            toast.success("Pembelian berhasil disimpan & Stok bertambah!");
            router.push('/admin/inventaris/pembelian'); 

        } catch (error: any) {
            console.error(error);
            toast.error(error.response?.data?.message || "Gagal menyimpan transaksi.");
        } finally {
            setIsSaving(false);
        }
    };

    return (
        <div className="space-y-6 pb-20 max-w-5xl mx-auto">
            <div className="flex items-center gap-4">
                <Link href="/admin/inventaris/pembelian">
                    <Button variant="outline" size="icon"><ArrowLeft className="h-4 w-4" /></Button>
                </Link>
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Catat Pembelian Baru</h1>
                    <p className="text-gray-500 text-sm">Input stok masuk dari supplier.</p>
                </div>
            </div>

            <form onSubmit={handleSubmit} className="space-y-6">
                
                <Card>
                    <CardHeader className="pb-3 border-b mb-3"><CardTitle className="text-base">Informasi Faktur</CardTitle></CardHeader>
                    <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <Label>Supplier <span className="text-red-500">*</span></Label>
                            <Select onValueChange={(v) => setFormData({...formData, supplier_id: v})}>
                                <SelectTrigger><SelectValue placeholder="Pilih Supplier" /></SelectTrigger>
                                <SelectContent>
                                    {suppliers.map(s => (
                                        <SelectItem key={s.id} value={s.id.toString()}>{s.nama_supplier}</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>
                        <div className="space-y-2">
                            <Label>Tanggal Pembelian <span className="text-red-500">*</span></Label>
                            <Input type="date" value={formData.tanggal_pembelian} onChange={e => setFormData({...formData, tanggal_pembelian: e.target.value})} required />
                        </div>
                        <div className="space-y-2">
                            <Label>Metode Pembayaran <span className="text-red-500">*</span></Label>
                            <Select onValueChange={(v) => setFormData({...formData, payment_method_id: v})}>
                                <SelectTrigger><SelectValue placeholder="Pilih Metode" /></SelectTrigger>
                                <SelectContent>
                                    {payments.map(p => (
                                        <SelectItem key={p.id} value={p.id.toString()}>{p.nama_metode}</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>
                        <div className="space-y-2">
                            <Label>No. Faktur Supplier (Opsional)</Label>
                            <Input placeholder="Cth: INV-001" value={formData.no_faktur_supplier} onChange={e => setFormData({...formData, no_faktur_supplier: e.target.value})} />
                        </div>
                        <div className="space-y-2 col-span-2">
                            <Label>Catatan</Label>
                            <Textarea placeholder="Keterangan tambahan..." value={formData.catatan} onChange={e => setFormData({...formData, catatan: e.target.value})} />
                        </div>
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader className="flex flex-row justify-between items-center pb-3 border-b mb-3">
                        <CardTitle className="text-base">Daftar Barang</CardTitle>
                        <Button type="button" variant="secondary" size="sm" onClick={addItem}><Plus className="h-4 w-4 mr-2" /> Tambah Baris</Button>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        {items.map((item, index) => (
                            <div key={index} className="flex flex-col md:flex-row gap-4 items-end border-b pb-4 last:border-0 last:pb-0">
                                {/* Dropdown Bahan Baku */}
                                <div className="flex-1 space-y-2 w-full">
                                    <Label className="text-xs text-gray-500">Bahan Baku</Label>
                                    <Select value={item.bahan_baku_id} onValueChange={(v) => updateItem(index, 'bahan_baku_id', v)}>
                                        <SelectTrigger><SelectValue placeholder="Pilih Barang..." /></SelectTrigger>
                                        <SelectContent>
                                            {bahanBakus.map(b => (
                                                <SelectItem key={b.id} value={b.id.toString()}>
                                                    {b.nama_bahan_baku} <span className="text-gray-400 text-xs">({b.satuan?.nama_satuan}) - Sisa: {Number(b.stok_saat_ini)}</span>
                                                </SelectItem>
                                            ))}
                                        </SelectContent>
                                    </Select>
                                </div>
                                
                                <div className="w-32 space-y-2">
                                    <Label className="text-xs text-gray-500">Jumlah</Label>
                                    <Input type="number" placeholder="Qty" value={item.jumlah} onChange={e => updateItem(index, 'jumlah', e.target.value)} min="0" />
                                </div>
                                
                                <div className="w-40 space-y-2">
                                    <Label className="text-xs text-gray-500">Harga Beli (@)</Label>
                                    <Input type="number" placeholder="Rp" value={item.harga_beli_satuan} onChange={e => updateItem(index, 'harga_beli_satuan', e.target.value)} min="0" />
                                </div>
                                
                                <div className="w-40 space-y-2 pb-2 text-right">
                                    <Label className="text-xs text-gray-500 block">Subtotal</Label>
                                    <span className="font-bold text-gray-900">
                                        {new Intl.NumberFormat('id-ID').format(item.subtotal)}
                                    </span>
                                </div>
                                
                                <Button type="button" variant="ghost" size="icon" className="text-red-500 hover:bg-red-50 mb-1" onClick={() => removeItem(index)} disabled={items.length === 1}>
                                    <Trash2 className="h-4 w-4" />
                                </Button>
                            </div>
                        ))}

                        <div className="flex justify-end items-center gap-4 pt-6 mt-4 border-t">
                            <span className="text-lg font-medium text-gray-600">Total Faktur:</span>
                            <span className="text-3xl font-bold text-[#D93F21]">
                                {new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR' }).format(grandTotal)}
                            </span>
                        </div>
                    </CardContent>
                </Card>

                <div className="flex justify-end">
                    <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b] w-full md:w-auto px-8 py-6 text-lg shadow-lg">
                        {isSaving ? <Loader2 className="h-5 w-5 animate-spin mr-2" /> : <Save className="h-5 w-5 mr-2" />}
                        Simpan Transaksi
                    </Button>
                </div>
            </form>
        </div>
    );
}